:root{
    --color-primario:#61dafb;
    --color-secundario:#282c34;
    --color-rojo:#db483e;
    --color-text: #eee;
}

html{
    box-sizing: border-box;
    font-size: 16px;
    font-family: sans-serif;
    background-color: var(--color-secundario);
    color: var(--color-text);
}

*::after, *::before {
    box-sizing: inherit;
}

.text-color{
    color: var(--color-primario);
}

input[type="text"]{
    border: thin solid #dedede;
    border-radius: 5px;
    padding: 0.75rem;
    margin-bottom: 1rem;
    outline: none;
    display: block;
    width: 100%;
    font-size: 1rem;
    line-height: 1;
    background-color: transparent;
    color: var(--color-text);
    box-sizing: border-box;
}

input[type="submit"],
input[type="reset"],
table button{
    border: thin solid #444;
    border-radius: 5px;
    padding: 0.75rem;
    margin-bottom: .5rem;
    outline: none;
    display: block;
    font-weight: bold;
    font-size: 1rem;
    line-height: 1;
    background-color: transparent;
    text-transform: none;
    text-decoration: none;
    text-align: center;
    cursor: pointer;
    width: 100%;
    color: #444;
    transition: .3s all ease;
}

input[type="submit"]:hover,
input[type="reset"]:hover{
    border: thin solid var(--color-primario);
    color: var(--color-primario);
}

.container-boton-form{
    display: flex;
}

form{
    margin-bottom: 1rem;
}

hr{
    margin: 2rem auto;
}

table{
    border-spacing: 0;
    border-collapse: collapse;
    width: 100%;
}

th,td{
    text-align: left;
    padding: .5rem;
    border-bottom: thin solid #444;
}

thead th{
    color: var(--color-primario);
}

table button {
    margin: 0;
}

table button:nth-child(1):hover{
    color: var(--color-primario);
    border: thin solid var(--color-primario);
}

table button:nth-child(2):hover{
    color: var(--color-rojo);
    border: thin solid var(--color-rojo);
}

@media screen and (min-width: 820px) {
    .grid-1-2{
        display: grid;
        grid-template-columns: minmax(350px,600px) minmax(350px,1000px);
        gap: 2rem;
        align-items: start;
        justify-content: center;
    }

    .titulo{
        text-align: center;
        opacity: .7;
    }
}
